@import url(https://fonts.googleapis.com/css?family=Open+Sans:400|Raleway:300);
body{padding-top:50px;font-family:"Open Sans",Helvetica,arial,sans-serif;text-align:center;background-color:#eee;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*,*:before,*:after{box-sizing:border-box}*:before,*:after{z-index:-1}
.App{margin:auto;min-width:300px}.App h1,.App h4{font-family:"Raleway","Open Sans",sans-serif;margin:0;line-height:1}.App #board{display:flex;flex-direction:row;flex-wrap:wrap;align-items:center;justify-content:center}.App .grid{display:grid;grid-template-columns:repeat(5, 1fr);grid-auto-rows:20%;justify-content:center;margin:2vw}.App .tile{display:flex;justify-content:center;align-items:center;width:100%;height:100%;font-size:calc(7px + 1vw);padding:7%;cursor:pointer}.App .tile--set{background:#ffeb3b}.App .tile--win{background:#69bf94}.App .content{display:flex;justify-content:center;align-items:center;width:100%;height:100%;font-weight:bold;font-size:calc(50px + 1vw);padding:7%;cursor:pointer;transition:.5s ease-in-out}@media(max-width: 500px){.App .content{font-size:calc(20px + 2vw);padding:auto}}
.fade-in{-webkit-animation:translate-left-center 1s;animation:translate-left-center 1s}.fade-out{-webkit-animation:translate-center-rigth 1s;animation:translate-center-rigth 1s}.flip-animation{-webkit-animation:mirrorflip 1s 0s linear infinite;animation:mirrorflip 1s 0s linear infinite}@-webkit-keyframes translate-left-center{from{-webkit-transform:translateX(-100%);transform:translateX(-100%)}to{-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes translate-left-center{from{-webkit-transform:translateX(-100%);transform:translateX(-100%)}to{-webkit-transform:translateX(0);transform:translateX(0)}}@-webkit-keyframes translate-center-rigth{from{-webkit-transform:translateX(0%);transform:translateX(0%)}to{-webkit-transform:translateX(100%);transform:translateX(100%)}}@keyframes translate-center-rigth{from{-webkit-transform:translateX(0%);transform:translateX(0%)}to{-webkit-transform:translateX(100%);transform:translateX(100%)}}@-webkit-keyframes mirrorflip{0%{transform:scale(1, 1);-webkit-transform:scale(1, 1);-moz-transform:scale(1, 1);-o-transform:scale(1, 1);-ms-transform:scale(1, 1)}50%{transform:scale(-1, 1);-webkit-transform:scale(-1, 1);-moz-transform:scale(-1, 1);-o-transform:scale(-1, 1);-ms-transform:scale(-1, 1)}100%{transform:scale(1, 1);-webkit-transform:scale(1, 1);-moz-transform:scale(1, 1);-o-transform:scale(1, 1);-ms-transform:scale(1, 1)}}@keyframes mirrorflip{0%{transform:scale(1, 1);-webkit-transform:scale(1, 1);-moz-transform:scale(1, 1);-o-transform:scale(1, 1);-ms-transform:scale(1, 1)}50%{transform:scale(-1, 1);-webkit-transform:scale(-1, 1);-moz-transform:scale(-1, 1);-o-transform:scale(-1, 1);-ms-transform:scale(-1, 1)}100%{transform:scale(1, 1);-webkit-transform:scale(1, 1);-moz-transform:scale(1, 1);-o-transform:scale(1, 1);-ms-transform:scale(1, 1)}}
button{text-decoration:none;color:#000}[class^=btn-]{position:relative;display:block;overflow:hidden;width:125px;height:40px;max-width:125px;margin:1rem auto;text-transform:uppercase;border:1px solid currentColor;background-color:rgba(0,0,0,0)}@media(max-width: 500px){[class^=btn-]{text-transform:none}}.btn-0{width:100%;height:100%;margin:auto;border:none;color:#81429e;cursor:pointer;font-size:calc(5px + 1vw)}.btn-0:after,.btn-0:before{transition:.5s ease-in-out}.btn-0:before{content:"";position:absolute;top:0;right:0;bottom:0;left:0;background-color:#401455;-webkit-transform:translateX(-100%);transform:translateX(-100%)}.btn-0:hover{color:tint(#81429e, 75%)}.btn-0:hover:before{-webkit-transform:translateX(0);transform:translateX(0)}@media(max-width: 700px){.btn-0{padding:20px}}.btn-1{width:25%;color:#81429e;cursor:pointer;transition:.5s ease-in-out}.btn-1:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}
[class^=box-]{position:relative;display:block;text-align:center;justify-content:center;overflow:hidden;border:1px solid currentColor}.box-0{border:none;cursor:pointer;outline:solid 1px #81429e;transition:outline .6s linear;margin:.5em}.box-0:hover{outline-width:5px}.box-1{border:3px solid #81429e;aspect-ratio:2/1;width:25%;margin:1rem auto}
canvas{position:fixed;top:0;left:0;overflow-y:hidden;overflow-x:hidden;height:100vh;width:100vw;margin:0;pointer-events:none}
